import React from 'react'

function Image({
    image,
    removeImage

}) {
  return (
    <div className="image">
        <span onClick={removeImage} value={image} style={{cursor:'pointer',}}>&times;</span>
        <div>
            <img src={image} className="img-fluid" width="92px" height="125px"/>
        </div>
        
        
    </div>
  )
}

export default Image;
